@font-face {
    src: url('Inter-Regular.woff') format('woff'), url('Inter-Regular.woff2') format('woff2');
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
}

@font-face {
    src: url('Inter-Medium.woff') format('woff'), url('Inter-Medium.woff2') format('woff2');
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
}

@font-face {
    src: url('Inter-Black.woff2') format('woff2'), url('Inter-Black.woff') format('woff');
    font-family: 'Inter';
    font-style: normal;
    font-weight: 900;
}
